import { createSlice, PayloadAction } from "@reduxjs/toolkit";
import { call, takeEvery, put } from "redux-saga/effects";
import mainService from "./api/api";

type MyWebType = {
  currentUser: string;
  quote: string;
  cockTail: {
    name: string;
    instruction: string;
  } | null;
};

const myWebInitialState: MyWebType = {
  currentUser: "",
  quote: "",
  cockTail: null,
};

const myWebSlice = createSlice({
  name: "myweb",
  initialState: myWebInitialState,
  reducers: {
    getQuote() {},
    setQuote(state, action: PayloadAction<{ text: string; author: string }>) {
      const { text, author } = action.payload;
      state.quote = text + " " + (author !== null ? "@" + author : "");
    },
    setCurrentUser(state, action: PayloadAction<string>) {
      state.currentUser = action.payload;
    },
    getCocktail() {},
    setCockTail(state, action: PayloadAction<any>) {
      console.log(action.payload);
      state.cockTail = action.payload;
    },
  },
});

function* getQuoteWorker() {
  try {
    const { text, author } = yield call(mainService.callAdvice);
    yield put(myWebActionCreators.setQuote({ text, author }));
  } catch (err) {
    console.log(err);
  }
}

function* getCockTailWorker() {
  try {
    const cockTail = yield call(mainService.getCockTail);
    const cockTailData = {
      name: cockTail.strDrink,
      instruction: cockTail.strInstructions,
    };
    yield put(myWebActionCreators.setCockTail(cockTailData));
  } catch (err) {
    console.log(err);
  }
}

export function* myWebSaga() {
  yield takeEvery(myWebActionCreators.getQuote, getQuoteWorker);
  yield takeEvery(myWebActionCreators.getCocktail, getCockTailWorker);
}

export const myWebActionCreators = {
  ...myWebSlice.actions,
};

export const myWebReducer = myWebSlice.reducer;
export type RootState = ReturnType<typeof myWebReducer>;
