import React, { useEffect, useRef, useState } from "react";
import "react-typed/dist/animatedCursor.css";
import Slider from "react-slick";
import styled from "styled-components";
import { connect } from "react-redux";
import { myWebActionCreators, RootState } from "./modules/myWeb";
import "./App.scss";
import Main from "./container/Main/main";
import AboutMe from "./container/aboutMe.container";
import Projects from "./container/projects.container";
import Contact from "./container/contact.container";

type myWebAppType = {
  currentUser: string;
  quote: string;
  cockTail: {
    name: string;
    instruction: string;
  } | null;
};

type myWebType = ReturnType<typeof mapStateToProps> &
  typeof dispatchProps &
  myWebAppType;

const mapStateToProps = (state: RootState) => ({
  currentUser: state.currentUser,
  quote: state.quote,
  cockTail: state.cockTail,
});

const dispatchProps = {
  getQuote: myWebActionCreators.getQuote,
  setCurrentUser: myWebActionCreators.setCurrentUser,
  getCocktail: myWebActionCreators.getCocktail,
};

const Container = styled.div`
  margin: 0;
  background-color: rgb(24, 24, 24);
  padding: 60px 40px 0px 60px;
  .slick-dots {
    display: flex !important;
    top: 10%;
    right: 0%;
    width: 0% !important;
    bottom: 0px !important;
    flex-direction: column !important;
    align-items: flex-end !important;
  }
  .slick-dots li button:before {
    color: gray !important;
  }
  .slick-dots li button:after {
    color: white !important;
  }

  .slick-arrow {
    display: none !important;
  }
`;

function App({
  cockTail,
  currentUser,
  quote,
  setCurrentUser,
  getQuote,
  getCocktail,
}: myWebType) {
  var settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
    vertical: true,
    draggable: true,
  };
  return (
    <Container>
      <Slider {...settings}>
        <Main
          quote={quote}
          currentUser={currentUser}
          setCurrentUser={setCurrentUser}
          getQuote={getQuote}
          getCocktail={getCocktail}
          cockTail={cockTail}
        ></Main>
        <AboutMe></AboutMe>
        <Projects></Projects>
        <Contact></Contact>
      </Slider>
    </Container>
  );
}

export default connect(mapStateToProps, dispatchProps)(App);
